.body{
    text-align: center;
    background-color: rgb(255, 193, 100);
    margin-top: 0;
    border-style:double;
    border-width : 6px;
    border-color :rgb(247, 164, 40);
    border-radius : 20px;
}

.addMessage{
    margin : 20px;
    padding : 10px;
    color : rgb(0, 85, 0);
    background-color : rgb(0, 255, 0);
    border-style : solid;
    border-width : 1px;
    border-color : rgb(14, 31, 14);
    border-radius : 20px;
    text-align: center;
}

.deleteMessage{
    margin: 20px;
    padding: 10px;
    color: rgb(85, 6, 0);
    background-color: rgb(255, 51, 0);
    border-style: solid;
    border-width: 1px;
    border-color: rgb(31, 17, 14);
    border-radius: 20px;
    text-align: center;
}